body.lindsay-granger {
  font-family: 'Cochin', 'EB Garamond', 'transitional', 'serif';
  font-size: 3vh;
  display: flex;
  justify-content: center;
  background: black;
  color: white;
}

.space, .purple, .wall, .darkness {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10px;
  width: 10px;
  font-size: 8px;
}

.panel-body {
  background: black;
}

.wall {
  -webkit-animation: lighting 1s infinite;
          animation: lighting 1s infinite;
  border-radius: 20%;
}

.darkness {
  background: black;
}

.darkness i {
  color: black;
}

.player {
  color: red;
}

.hufflepuff {
  color: yellow;
}

.ravenclaw {
  color: blue;
}

.slytherin {
  color: green;
}

.boss {
  color: white;
}

.wand {
  color: white;
}

.potion {
  color: green;
}

.book {
  color: purple;
}

.door {
  color: yellow;
}

.black-font {
  color: black;
}

#board, #board-cloaked {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 540px;
  margin: auto;
  background: #d98c8c;
}

#board-cloaked {
  background: #9999ff;
}

.start-button {
  font-size: 5vw;
}

.header {
  position: fixed;
  top: 0;
  font-size: 5vw;
  width: 100vw;
}

.start-panel {
  width: 100vw;
}

.header-placeholder {
  font-size: 5vw;
}

.message {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.center-row {
  display: flex;
  justify-content: center;
}

#stats {
  height: 100px;
  display: block;
  font-size: 2vh;
}

#stats-panel {
  width: 270px;
  border: solid black 0;
}

.row {
  justify-content: space-around;
}

.hide-icon {
  visibility: hidden;
}

