.center-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.border-none {
  border: black solid 1px;
}


@media (orientation: landscape) {
	:root {
		--board-width: 95vh;
		--cell-width: calc(var(--board-width)/70);
	}
}	

@media (orientation: portrait) {
	:root {
		--board-width: 95vw;
		--cell-width: calc(var(--board-width)/70);
	}
}		

body.light-bright {
  -webkit-user-select: none; /* Safari 3.1+ */
  -moz-user-select: none; /* Firefox 2+ */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Standard syntax */
  font: black;
}

.btn-toolbar {
	display: flex;
	justify-content: center;
}

.btn {
	width: 15vw;
	font-size: 2vw;
}

.dropdown-menu, .dropdown-header {
	font-size: 2vw;
}

.buttons {
	margin: auto;
}

.bulb {
	border-radius: 50%;
	width: var(--cell-width);
	height: var(--cell-width);
}

#board {
	margin: auto;
	background: black;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
	width: var(--board-width);
}

.block, .choices, #selected-list, #to-print {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

i {
  font-size: 1.25em; }

.navbar {
  position: fixed;
  width: 100%; }

.block {
  font-size: 14px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: #8c8cd9;
  color: #d9d9f2;
  border: solid #6666cc 1px;
  width: 16.666667%;
  text-align: center;
  justify-content: space-around; }

.block-title {
  display: none; }

.choices {
  color: black;
  justify-content: flex-start;
  background-color: white;
  border-radius: 10px;
  padding: 5px;
  font-size: 12px; }

#interests {
  border: none;
  height: 100px;
  flex-wrap: wrap; }
  #interests span {
    text-align: left; }
    #interests span input {
      width: 15px; }

.emoji {
  font-size: 20px;
  color: #222; }

.mobile-day-time {
  background: white;
  color: #6666cc;
  display: none;
  padding: 10px; }

.day-time {
  padding: 10px; }

.name {
  padding: 5px; }

.notes {
  color: red;
  font-style: italic; }

.selected-name {
  font-weight: bold; }

#clubs {
  height: 100%;
  overflow: auto; }

#email-print {
  display: flex;
  justify-content: space-around; }

#filters {
  background: white;
  color: #888;
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  display: none;
  flex-direction: row;
  justify-content: space-around; }

#form {
  position: absolute;
  top: 50px;
  left: 0px;
  z-index: -1;
  width: 100vw;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start; }

#print-button, #email-button {
  width: 40%;
  border-radius: 5px; }

#selected-list {
  position: relative;
  width: 100%;
  left: 0;
  top: 0;
  visibility: hidden;
  justify-content: space-around; }

#to-print {
  justify-content: space-around;
  overflow-y: auto; }

#schedule {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap; }

@media print {
  html * {
    visibility: hidden;
    height: 42px; }
  #to-print, #to-print * {
    visibility: visible; }
  #to-print {
    font-size: 30px;
    position: absolute;
    left: 0;
    top: 0;
    padding: 20px; } }

@media (max-width: 768px) {
  #menu {
    visibility: visible;
    display: inline-block; }
  .block {
    width: 100vw; }
  .block-title {
    display: flex;
    background: white;
    color: #6666cc;
    height: 40px;
    justify-content: center;
    align-items: center; }
  .day-time, .mbs {
    display: none; }
  .mobile-day-time {
    display: inline-block; }
  .block {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    border-left: none;
    border-right: none; }
  .club {
    width: 33%; }
  #closeClubs {
    display: none; } }

body, html {
	width: 100vw;
}

button:focus, input:focus, select:focus {
	outline: none;
}

input, div.ddmenu, .li-buttons, .total-cost, .total-label {
	width: 20%;
	text-align: center;
	border-radius: 5px;
	border: grey solid 1px;
}

.li-buttons {
	display: flex;
	border: none;
}

button {
	flex: 1 1 auto;
	text-align: center;
	border-radius: 5px;
	border: grey solid 1px;
}

div.ddmenu {
	border: none;
}

.add-line-item {
	background: green;
}

.better-select, .upcharge-select {
	display: flex;
	position: relative;
	flex-direction: column;
	outline: none;
	width: 100%;
}

.ddbutton {
	background-color: white;
	width: 100%;
	height: 100%;
	text-align: center;
	border-radius: 5px;
	border: grey solid 1px;
}

.delete-line-item {
	background: red;
}

.line-item {
	flex: 1 1;
	display: flex;
	flex-direction: row;
	align-items: stretch;
	justify-content: space-around;
}

.select-menu, .upcharge-menu {
	z-index: 2;
	position: absolute;
	top: 0;
	left: 0;
	display: none;
	flex-direction: column;
	background: white;
	min-width: content;
	border: gray solid 1px;
	overflow: auto;
	border-radius: 5px;
	white-space: normal;
	width: 100%;
	min-width: -webkit-max-content;
	min-width: -moz-max-content;
	min-width: max-content;
}

.select-menu input, .upcharge-menu input {
	text-align: left;
	padding-left: 5px;
	width: 100%;
	border-left: none;
	border-right: none;
	border-bottom: none;
}

.total-cost {
	color: white;
	background-color: grey;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;
}

.total-label {
	flex-basis: 60%;
	text-align: center;
}

.total-output {
	flex-basis: 20%;
}

.totals {
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
}

.choice {
	width: 100%;
	border: none;
	text-align: left;
	white-space: nowrap;
}

.choice:hover {
	background: #EEE;
}

#calculator {
	margin: auto;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

#container {
	width: 80%;
	margin: 50px auto;
}


